import React, { useEffect, useState } from "react";
import { Graph } from '../../types/graph';
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import { useParams } from "react-router-dom";
import moment from 'moment';
import { getGraphData } from '../../api/GraphData';
import { Loader } from '../Loader/Loader';

// type Props = {
//     graphData: Graph[];
//     setPeriod: (variable: number) => void;
// };

interface ICustomToolip {
    active: boolean;
    payload: any;
}

export const GraphPage: React.FC = () => {

    const [graphData, setGraphData] = useState<Graph[]>([]);
    const [period, setPeriod] = useState(0);
    const [isLoading, setIsLoading] = useState(false);
    const [isError, setIsError] = useState(false);
    // const [visibleData, setVisibleData] = useState<Graph[]>([]);

    const { id } = useParams();

    useEffect(() => {
        setIsLoading(true);
        if (id) {
            getGraphData(id)
                .then(response => setGraphData(response.data))
                .catch(() => setIsError(true))
                .finally(() => setIsLoading(false))
        }
    }, [id]);

    const sortedData = (array: Graph[]) => {
        const newList = array.sort((a, b) => {
            return +moment(a.date) - +moment(b.date);
        });

        newList.map(graph => {
            graph.date = String(graph.date)
                .slice(0, 10)
                .replace('-', '.')
                .replace('-', '.');

            return 0;
        })

        if (period === 0) {
            return newList;
        }

        return newList.slice(graphData.length - period, graphData.length);
    };

    const CustomTooltip = ({ active, payload }: ICustomToolip) => {
        if (active) {
            return (
                <div className="custom-tooltip" style={{
                    padding: "10px",
                    backgroundColor: "#f5f5f5",
                    border: "2px solid #007AFF",
                }}
                >
                    <p className="label">{`${payload[0].value}`}</p>
                </div>
            );
        }

        return null;
    }

    return (
        <>
            <div className="top">
                <h1 className="top__header">Revenue</h1>
                <div className="top__buttons">
                    <button
                        className="top__buttons-1"
                        onClick={() => setPeriod(14)}
                    >
                        Week
                    </button>
                    <button
                        className="top__buttons-2"
                        onClick={() => setPeriod(56)}
                    >
                        Month
                    </button>
                    <button
                        className="top__buttons-3"
                        onClick={() => setPeriod(520)}
                    >
                        Year
                    </button>
                </div>
            </div>
            {graphData.length > 0
                ? (
                    <ResponsiveContainer width="100%" height={200}>
                        <AreaChart
                            width={1000}
                            height={200}
                            data={sortedData(graphData)}
                            margin={{
                                top: 10,
                                right: 30,
                                left: 0,
                                bottom: 0,
                            }}
                        >
                            <CartesianGrid />
                            <XAxis dataKey={"date"} />
                            <YAxis domain={[0, 1000]} />
                            <Tooltip content={<CustomTooltip active={true} payload={undefined} />} />
                            <Area connectNulls type='monotone' dataKey="curency" stroke="#007AFF" fill="#007AFF" />
                        </AreaChart>
                    </ResponsiveContainer>
                )
                : (
                    <Loader />
                )}

            <h1>Total</h1>
        </>
    )
}